<template>
  <div class="Top2" style="min-height:100vh">
    <!-- タイマー部 -->
    <div>
      <span class="timerParent" style="font-family: 'M PLUS Rounded 1c';" :style="timerFontSize">{{timerLabel}}</span>
    </div>

    <div v-show="this.$root.status == 'INIT'">
      <span class="font_xl">ボタンをおして勉強を開始してください。</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_start.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" style="width:150px" v-on:click="pushStatus('START')">勉強を開始する</button>
    </div>
    <div v-show="this.$root.status == 'START'">
      <span class="font_xl">お勉強がんばってください！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_play.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('PAUSE')">休憩する</button>
      <button class="btnBase" v-on:click="pushStatus('STOP')">勉強を終了する</button>
    </div>
    <div v-show="this.$root.status == 'PAUSE'">
      <span class="font_xl">休憩もたいせつです！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_pause.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('RESUME')">再開する</button>
      <button class="btnBase" v-on:click="pushStatus('STOP')">勉強を終了する</button>
    </div>
    <div v-show="this.$root.status == 'STOP'">
      <span class="font_xl">おつかれさまでした！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_stop.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('RESET')">リセットする</button>
      <button class="btnBase" v-on:click="pushStatus('RESET')">書き込む</button>
    </div>
    
    <div class="sp_line_l" />

    <!-- 各種新着一覧 -->
    <p class="font_l">勉強報告新着</p>
    <ListFrame kbn="REPORT" isLatest="true" :list="this.reportList" @rowAction="rowAction"></ListFrame>
    <button type="button" class="btnBase" @click="pushMore(0)">もっと見る</button>
    <div class="sp_line_m" />

    <p class="font_l">受験生新着</p>
    <ListFrame kbn="EXAMINEE" isLatest="true" :list="this.examineeList" @rowAction="rowAction"></ListFrame>
    <button type="button" class="btnBase" @click="pushMore(1)">もっと見る</button>
    <div class="sp_line_m" />

    <p class="font_l">愚痴新着</p>
    <ListFrame kbn="GUCHI" isLatest="true" :list="this.guchiList" @rowAction="rowAction"></ListFrame>
    <button type="button" class="btnBase" @click="pushMore(2)">もっと見る</button>
    <div class="sp_line_m" />
    
    <p class="font_l">相談・質問新着</p>
    <ListFrame kbn="SOUDAN" isLatest="true" :list="this.soudanList" @rowAction="rowAction"></ListFrame>
    <button type="button" class="btnBase" @click="pushMore(3)">もっと見る</button>
    <div class="sp_line_m" />

    <p class="font_l">なんでも新着</p>
    <ListFrame kbn="NANDEMO" isLatest="true" :list="this.nandemoList" @rowAction="rowAction"></ListFrame>
    <button type="button" class="btnBase" @click="pushMore(4)">もっと見る</button>
    <div class="sp_line_m" />

    <div class="sp_line_m" />

  </div>
</template>

<script>
import axios from 'axios'
import ListFrame from './ListFrame.vue';
export default {
  name: 'Top2',
  components: {
    ListFrame
  },
  data() {
    return {
      // selectedFrame : 0,
      // isSelectFrame : false,

      // labelArray : ["", "の勉強を始めました"],
      // labelArray1 : ["img_make_start.png", "の勉強を始めました"],
      // labelArray2 : ["img_make_stop.png", "の勉強を終了しました"],
 
      // isHowto : false,

      reportList : [],
      examineeList : [],
      guchiList : [],
      soudanList : [],
      nandemoList : [],
    }
  },
  created : function () {
  },
  computed : {
    isImg : function () {
      return (frameName, kbn) => {
        if (frameName == null) {
          return false;
        }
        if (frameName.indexOf("#") != -1) {
          if (kbn == 0) {
            return false;
          } else {
            return 'inline-block';
          }
        } else {
          if (kbn == 0) {
            return true;
          } else {
            return 'none';
          }
        }
      }
    },
    timerFontSize : function () {
      let fSize = this.$root.baseParentWidth / 6.5;
      if (fSize > 90) {
        fSize = 90;
      }
      return "font-size:" + fSize + "px";
    },
    timerLabel : function () {
      var hours = Math.floor(this.$root.timerValue / 3600);
      var minutes = Math.floor((this.$root.timerValue % 3600) / 60);
      var remainingSeconds = this.$root.timerValue % 60;

      var formattedTime = '';
      formattedTime += hours.toString().padStart(2, '0') + ':';
      formattedTime += minutes.toString().padStart(2, '0') + ':';
      formattedTime += remainingSeconds.toString().padStart(2, '0');
      return formattedTime;
    },
  },
  mounted () {
  },
  methods : {
    pushHowto : function () {
      this.isHowto = !this.isHowto;
    },
    pushBtn : function (name) {
      console.log(name);
    },
    pushMore : function (index) {
      if (index == 0) {
        this.$router.push("/report");
      } else if (index == 1) {
        this.$router.push("/examinee");
      } else if (index == 3) {
        this.$router.push("/guchi");
      } else if (index == 4) {
        this.$router.push("/soudan");
      } else if (index == 5) {
        this.$router.push("/nandemo");
      }
    },
    chFrame : function (index) {
      this.selectedFrame = index;
      this.isSelectFrame = false;
      localStorage.setItem("SEL_FRAME", this.frameList[this.selectedFrame]);
    },
    pushStatus : function (val) {
      this.$root.status = val;
      // this.labelArray = this.labelArray1;

      if (val == "RESET") {
        //リセット
        this.$root.timerValue = 0;
        this.$root.currentTime = 0;
        this.$root.startTime = "";
        this.$root.status = "INIT";
        localStorage.removeItem("START_TIME");
        localStorage.removeItem("CURRENT_SEC");
      } else if (val == "START") {
        //開始
        this.$root.calculateWindowWidth();
        this.$root.startTime = new Date();
        this.$root.currentTime = 0;
        this.$root.timerValue = 0;
        localStorage.setItem("START_TIME", this.$root.startTime.toLocaleString());
        localStorage.setItem("CURRENT_SEC", 0);
      } else if (val == "STOP") {
        //終了
        this.$root.currentTime = this.$root.timerValue;
        this.$root.startTime = null;
        localStorage.setItem("CURRENT_SEC", this.$root.currentTime);
        localStorage.removeItem("START_TIME");
        // this.labelArray = this.labelArray2;

        this.cHour = Math.floor(this.$root.currentTime / 3600);
        this.cMinute = Math.floor((this.$root.currentTime % 3600) / 60);
        this.cSecond = this.$root.currentTime % 60;
      } else if (val == "PAUSE") {
        //一時停止
        //これまでの秒数を保管
        let addVal = Math.floor((new Date() - this.$root.startTime) / 1000);
        this.$root.currentTime += addVal;
        this.$root.startTime = null;
        localStorage.setItem("CURRENT_SEC", this.$root.currentTime);
        localStorage.removeItem("START_TIME");
      } else if (val == "RESUME") {
        //再開
        this.$root.status = "START";
        this.$root.startTime = new Date();
        localStorage.setItem("START_TIME", this.$root.startTime.toLocaleString());
      }
      localStorage.setItem("STATUS", this.$root.status);
    },

    initAction : function () {
      this.$root.pageAction("TOP");
      
      let params = new URLSearchParams();
      params.append('page', "-1");
      params.append('userId', this.$root.userId);
      params.append('degree', JSON.stringify(this.$root.dispDegreeList));

      axios.post(this.$root.SERVER_PATH + "getLatestPostList", params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);
        if (resData["status"] == this.$root.API_SUCCESS) {
          //成功
          // this.postList = resData["postList"];

          this.reportList = resData["reportList"];
          this.examineeList = resData["examineeList"];
          this.guchiList = resData["guchiList"];
          this.soudanList = resData["soudanList"];
          this.nandemoList = resData["nandemoList"];
          console.log(this.reportList);
          
          this.$root.chkUserId(resData);
        }
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }


}
</script>

